@import url(https://fonts.googleapis.com/css2?family=Raleway:wght@500&display=swap);
body{margin:0;font-family:"Raleway", serif !important;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}

*,html{padding:0;margin:0}.icon{width:50px;height:50px;margin:1em 0em 1em 1em}.App{background:#69369E}

.about-container{margin-top:3.75em;color:white;height:600px;display:flex}.about-info{flex:2 1}.about-picture{flex:1 1}.pic{width:550px;height:550px}

.skills-container{background:#7951A8;height:300px;text-align:center}.skills-items{display:flex}

.projects-container{height:600px}.projects-container h1{text-align:center}.card-container{display:flex}.cards{margin:2em 0 2em 2em}

.contact-container{background:#7951A8;height:300px;text-align:center}

.nav{background:#69369E;font-size:1.2rem}

.footer-container{background:#232323;color:white;display:flex;align-items:center;justify-content:center;height:60px}.footer-creator h5{text-align:center;margin-bottom:0}

